@charset "UTF-8";
.tts-flex-1 {
  flex: 1 !important;
}

.tts-flex-2 {
  flex: 2 !important;
}

.tts-flex-3 {
  flex: 3 !important;
}

.tts-flex-4 {
  flex: 4 !important;
}

.tts-flex-5 {
  flex: 5 !important;
}

.tts-flex-6 {
  flex: 6 !important;
}

.tts-flex-7 {
  flex: 7 !important;
}

.tts-flex-8 {
  flex: 8 !important;
}

.tts-flex-9 {
  flex: 9 !important;
}

.tts-flex-10 {
  flex: 10 !important;
}

.tts-flex-11 {
  flex: 11 !important;
}

.tts-flex-12 {
  flex: 12 !important;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
}

.row {
  width: 100%;
}

@media screen and (min-width: 576px) {
  .only-on-sm {
    display: none !important;
  }
}

@media screen and (min-width: 768px) {
  .only-on-md {
    display: none !important;
  }
}

@media screen and (min-width: 992px) {
  .only-on-lg {
    display: none !important;
  }
}

.bg-primary {
  background-color: #0080ff !important;
}

.bg-secondary {
  background-color: #f4345f !important;
}

.only-dark-mode {
  display: none;
}

.tts-dark-mode .only-light-mode {
  display: none;
}
.tts-dark-mode .only-dark-mode {
  display: inline-block;
}

html {
  font-size: 14;
}

body {
  font-family: "Outfit", sans-serif;
  background-color: #fff;
  overflow: hidden;
  transition: display ease 200ms;
}

body.hi-dark-mode {
  background-color: #111111;
  color: #fff;
}

.hi-layout {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  width: 100vw;
  height: 100vh;
  transition: all 200ms ease;
}
@media screen and (min-width: 768px) {
  .hi-layout {
    grid-template-columns: 60px 1fr;
  }
}
@media screen and (min-width: 992px) {
  .hi-layout {
    grid-template-columns: 250px 1fr;
  }
}
.hi-layout__main {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 60px 1fr;
  overflow: hidden;
}
.hi-layout__content {
  overflow-y: scroll;
}
.hi-layout--scrollable {
  overflow-y: scroll;
}

.hi-layout__topbar {
  display: flex;
  transition: all 200ms ease;
  display: flex;
}
.hi-layout__topbar-section {
  display: flex;
  flex: 1;
  align-items: center;
  padding: 0 2rem;
}
.hi-layout__offcanvas-trigger {
  display: flex;
  justify-content: flex-end;
  font-size: 1.4rem;
  cursor: pointer;
}

.hi-dark-mode .hi-layout__topbar {
  background-color: #111111;
  color: #fff;
}

.hi-layout__sidebar {
  background-color: #f4345f;
  grid-template-rows: 60px 60px 1fr;
  grid-template-columns: 1fr;
  color: #f4f8fc;
  display: none;
}
@media screen and (min-width: 768px) {
  .hi-layout__sidebar {
    display: grid;
  }
}
.hi-layout__sidebar-oc-trigger {
  display: flex;
  align-items: center;
  height: inherit;
  justify-content: center;
  font-size: 1.2rem;
  cursor: pointer;
}
.hi-layout__logo {
  display: flex;
  align-items: center;
  justify-content: center;
}
.hi-layout__logo img {
  max-height: 50px;
}
.hi-layout__logo-full {
  display: none;
}

.hi-dark-mode .hi-layout__sidebar {
  background-color: #1f1e26;
  background-color: #161616;
  color: #f4f8fc;
}

.hi-navigation {
  display: none;
}
@media screen and (min-width: 992px) {
  .hi-navigation {
    display: block;
  }
}
.hi-navigation__item {
  text-decoration: none;
  display: flex;
  height: 40px;
  align-items: center;
  justify-content: center;
  color: #f4f8fc;
  position: relative;
}
.hi-navigation__item:hover {
  color: #ccdff1;
}
@media screen and (min-width: 992px) {
  .hi-navigation__item {
    justify-content: flex-start;
    padding: 0 20px;
  }
  .hi-navigation__item::after {
    content: "ˇ";
    position: absolute;
    right: 20px;
    font-size: 1.6rem;
    top: 10px;
  }
}
.hi-navigation__item--sub {
  height: 35px;
  padding-left: 0.1rem;
  font-size: 0.9rem;
}
.hi-navigation__item--sub::before {
  content: "--  ";
}
.hi-navigation__item--sub::after {
  content: "";
}
@media screen and (min-width: 992px) {
  .hi-navigation__item--sub {
    padding-left: 2rem;
  }
}
.hi-navigation__icon {
  font-size: 1.3rem;
}
@media screen and (min-width: 992px) {
  .hi-navigation__icon {
    font-size: 1rem;
    margin-right: 10px;
  }
}
.hi-navigation__title {
  display: none;
  transition: all 200ms ease;
  margin-top: -2px;
}
@media screen and (min-width: 992px) {
  .hi-navigation__title {
    display: block;
  }
}

.hi-offcanvas .hi-navigation {
  display: block;
}
.hi-offcanvas .hi-navigation__title {
  display: block;
}
.hi-offcanvas .hi-navigation__item {
  justify-content: flex-start;
}
.hi-offcanvas .hi-navigation__icon {
  font-size: 1rem;
  margin-right: 10px;
}

.hi-header {
  background-color: #f4f8fc;
  padding: 2rem 2rem;
  position: relative;
}
.hi-header__title {
  margin-bottom: 0;
}
.hi-header__subtitle {
  margin: 0;
}
.hi-header__toggle {
  position: absolute;
  right: 20px;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  background: #fff;
  color: #f4345f;
  top: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-50%);
  cursor: pointer;
  transition: all 200ms ease;
  font-size: 1.6rem;
}
.hi-header__toggle:hover {
  background-color: #f4345f;
  color: #f4f8fc;
}
.hi-header--collapsed {
  padding: 1em;
}
.hi-header--collapsed .hi-header__subtitle {
  display: none;
}
.hi-header--centered {
  text-align: center;
}
.hi-header--right {
  text-align: right;
}

.hi-dark-mode .hi-header {
  background-color: black;
  background-color: #111111;
}

.hi-yield {
  padding: 2rem 2rem;
}

.hi-section {
  display: flex;
  padding: 1rem 2rem;
  flex-direction: column;
  width: 100%;
}
.hi-section--hfull {
  height: 100vh;
}
.hi-section--vcentered {
  justify-content: center;
}
.hi-section--hcentered {
  align-items: center;
}

.hi-offcanvas {
  background: #f4345f;
  border: none;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.hi-offcanvas .offcanvas-header {
  color: #fff;
}

.hi-dark-mode .hi-offcanvas {
  background: #111111;
  border-right: 1px solid #070707;
  border-right: none;
  box-shadow: rgba(0, 0, 0, 0.04) 5px 5px 5px;
}

.hi-code {
  padding: 0 2rem;
  background-color: #0c0c0c;
  color: #fff;
  font-family: "PT Mono", monospace;
  font-size: 0.9rem;
}
.hi-code xmp {
  margin: 0;
  color: #fff;
}
.hi-code pre {
  margin: 0;
  padding: 0;
}
.hi-code .hljs,
.hi-code .hljs-attr {
  background: none;
  color: #fff;
}
.hi-code .hljs-string {
  color: #d2f0a6;
}
.hi-code .hljs-attr {
  color: #f5a2a9;
}
.hi-code .hljs-name {
  color: #f5a2a9;
}
.hi-code .hljs-tag {
  color: #f5a2a9;
}

.hi-btn {
  text-transform: uppercase;
  padding-left: 1em;
  padding-right: 1em;
  font-weight: 700;
  border-width: 2.5px;
  border-radius: 0.1rem;
  display: inline-flex;
}
.hi-btn i, .hi-btn__icon {
  margin-right: 0.3rem;
}
.hi-btn--rounded {
  border-radius: 5rem;
}
.hi-btn--icon-only i, .hi-btn--icon-only .hi-btn__icon {
  margin: 0;
}

.hi-toolbar {
  display: flex;
  justify-content: flex-start;
}
.hi-toolbar--centered {
  justify-content: center;
}
.hi-toolbar--right {
  justify-content: flex-end;
}
.hi-toolbar-wrapper {
  display: flex;
  height: 45px;
  display: inline-flex;
  background: #fff;
  overflow: hidden;
}
.hi-toolbar__item {
  min-width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  text-decoration: none;
  color: initial;
  position: relative;
}
.hi-toolbar__item::before {
  height: 50%;
  width: 1px;
  background-color: rgba(0, 0, 0, 0.04);
  content: "";
  position: absolute;
  left: 0;
}
.hi-toolbar__item:first-child::before {
  width: 0;
  height: 0;
}
.hi-toolbar__item:hover {
  background: rgba(0, 0, 0, 0.02);
}
.hi-toolbar__item--active {
  background: #f4345f;
  color: #fff;
}
.hi-toolbar--fluid {
  width: 100%;
}
.hi-toolbar--fluid .hi-toolbar-wrapper {
  width: 100%;
}
.hi-toolbar--fluid .hi-toolbar__item {
  flex: 1;
}
.hi-toolbar--slim .hi-toolbar-wrapper {
  height: 30px;
  font-size: 0.9em;
}

.hi-dark-mode .hi-toolbar-wrapper {
  background: #161616;
  background: none;
  border-radius: 2px;
}
.hi-dark-mode .hi-toolbar__item {
  color: #fff;
}
.hi-dark-mode .hi-toolbar__item::before {
  height: 50%;
  width: 1px;
  background-color: rgba(255, 255, 255, 0.04);
  content: "";
  position: absolute;
  left: 0;
}

.hi-table {
  background: white;
  width: 100%;
  border-radius: 4px;
  overflow-x: scroll;
  white-space: nowrap;
  padding-bottom: 1rem;
}
.hi-table table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
}
.hi-table thead {
  border-collapse: collapse;
  background-color: #f8f8f9;
}
.hi-table thead tr:hover {
  background-color: #f8f8f9;
}
.hi-table th {
  text-align: left;
  padding: 0.9rem 1rem;
  text-transform: uppercase;
  font-size: 0.7rem;
  white-space: nowrap;
}
.hi-table tr, .hi-table__tr {
  border-bottom: 1px solid #f4f4f4;
}
.hi-table tr:last-child, .hi-table__tr:last-child {
  border-bottom: none;
}
.hi-table tr:nth-child(even), .hi-table__tr:nth-child(even) {
  background-color: #f4f8fc;
}
.hi-table tr:hover, .hi-table__tr:hover {
  background: #fff5e8;
  color: #222;
  cursor: pointer;
}
.hi-table tr--selected, .hi-table__tr--selected {
  background: #ffeed9 !important;
  color: #222;
  cursor: pointer;
}
.hi-table tr td, .hi-table__tr td {
  padding: 0.5rem 1rem;
  font-size: 0.85rem;
}
.hi-table__meta {
  display: flex;
  height: 40px;
  align-items: center;
  padding: 0 1rem;
  margin-bottom: 1rem;
  justify-content: flex-end;
}

.hi-dark-mode .hi-table {
  background: none;
}
.hi-dark-mode .hi-table thead {
  background: #16151b;
}
.hi-dark-mode .hi-table thead tr:hover {
  background-color: #1f1e26;
  color: #fff;
}
.hi-dark-mode .hi-table tr {
  border-bottom: #1f1e26;
}
.hi-dark-mode .hi-table tr:nth-child(even) {
  background-color: #1f1e26;
}
.hi-dark-mode .hi-table tr:hover {
  background: #1f1e26;
  background: #fff5e8;
  color: #222;
  cursor: pointer;
}
.hi-dark-mode .hi-table tr td {
  padding: 0.5rem 1rem;
}

/* Responsive */
@media only screen and (max-width: 767px) {
  .hi-table {
    width: 100%;
  }
  .hi-table thead {
    display: none;
  }
  .hi-table th, .hi-table td {
    display: flex;
    align-items: center;
    justify-content: left;
  }
  .hi-table tr:first-child {
    border-top: 1px solid #f4f4f4;
    border-top: none;
  }
  .hi-table tr:last-child {
    border-bottom: none;
  }
  .hi-table tr:nth-child(even) {
    background: #f4f8fc;
  }
  .hi-table td {
    font-weight: 400 !important;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .hi-table td::before {
    content: attr(data-col);
    width: 100%;
    font-weight: 700;
  }
}
.hi-card {
  border-radius: 0.5rem;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border: 1px solid #f4f8fc;
  background: #fff;
}
.hi-card__main {
  padding: 1rem;
}
.hi-card__header {
  padding: 1rem;
  display: flex;
  border-bottom: 1px solid #f4f8fc;
}
.hi-card__title {
  flex: 1;
  font-weight: 700;
  display: flex;
  align-items: center;
}
.hi-card__menu {
  flex: 1;
  max-width: 60px;
  display: flex;
  justify-content: flex-end;
  font-size: 1.5rem;
  cursor: pointer;
}
.hi-card__menu .dropdown-menu,
.hi-card__menu .dropdown-divider {
  border-color: #f4f8fc;
}
.hi-card__menu .dropdown-item:hover {
  background-color: #fff5e8;
}

.hi-dark-mode .hi-card {
  background-color: #111111;
  box-shadow: rgba(0, 0, 0, 0.04) 5px 5px 5px;
  border: none;
}
.hi-dark-mode .hi-card__header {
  border-bottom: 1px solid #0c0c0c;
}